<template>
    <v-layout justify-center>
      <v-flex xl12 sm12>
        <v-card>
                    <v-card-title>
            SERVER USA
          </v-card-title>
          <v-container
            fluid
            grid-list-md
          >
            <v-layout row wrap>
              <v-flex
                v-for="(item,index) in items"
                v-bind="{ [`xs${item.flex}`]: true }"
                :key="item.name"
                v-model="items"
              >
                <v-card>
                  <v-card-media
                    src="https://i.pinimg.com/originals/a2/a1/7a/a2a17a270659686e11b8fc837f06f0f9.png"
                  height="200px">
                </v-card-media>
                    <v-container
                      fill-height
                      fluid
                      pa-2
                    >
                      <v-layout fill-height>
                        <v-flex xs12 align-end flexbox>
                          <span class="headline dark--text" v-text="item.name"></span>
                        </v-flex>
                      </v-layout>
                    </v-container>
  
          <v-card-actions>
            <v-btn fab dark small color="red" @click.native="removeItem(item.name, index)">
              <v-icon dark>remove</v-icon>
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn icon @click="item.show = !item.show">
              <v-icon>{{ item.show ? 'keyboard_arrow_down' : 'keyboard_arrow_up' }}</v-icon>
            </v-btn>
          </v-card-actions>
  
          <v-slide-y-transition>
            <v-card-text v-show="item.show">
              <span><b>IP:</b></span>
              <span v-text="item.endpoints.minecraft"></span>
              <br>
              <span><b>RCon IP:</b></span>
              <span v-text="item.endpoints.rcon"></span>
            </v-card-text>
          </v-slide-y-transition>

                </v-card>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-flex>
    <v-btn
      fab
      bottom
      right
      color="pink"
      dark
      fixed
      @click.native="createItem()"
    >
      <v-icon>add</v-icon>
    </v-btn>
<v-snackbar
      v-model="snackbar"
      :color= "typeSnack"
      :multi-line="mode === 'multi-line'"
      :timeout="timeout"
      :vertical="mode === 'vertical'"
    >
      {{ snackText }}
      <v-btn
        dark
        flat
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </v-snackbar>
    </v-layout>
</template>

<script>
import AppVue from '@/App.vue';
import axios from "axios";
import CentralPaneUS from '@/views/CentralPaneUS.vue';
var objcardCentral = null;

export default {
  name: "cardCentralUS",
  data: () => ({
    snackbar: false,
    typeSnack: "success",
    snackText: "",
    items: []
  }),
  created: function(){
    objcardCentral = this;
    return;
  },
  mounted() {
    var config = {
      headers: {'Access-Control-Allow-Origin': '*'}
    };
    axios({ method: "GET", "url": "http://lista-k8s.eastus.cloudapp.azure.com:5000/api/server" }, config).then(result => {
        console.log(result.data);
      result.data.forEach(element => {
        element.show = true;
      });
      objcardCentral.items = result.data;
    }, error => {
        console.error(error);
    });
  },
  methods: {
    removeItem(data, indice) {
      var config = {
        headers: {'Access-Control-Allow-Origin': '*'}
      };
      var urlString = "http://lista-k8s.eastus.cloudapp.azure.com:5000/api/server/delete/" + data;
        axios({ method: "GET", "url": urlString }, config).then(result => {
        console.log(result.data);
        objcardCentral.typeSnack = "success";
        objcardCentral.snackText = "Server Removed" + result.data;
        objcardCentral.snackbar = true;
      }, error => {
          console.error(error);
      });
      return;
    },
    createItem(){
      var config = {
        headers: {'Access-Control-Allow-Origin': '*'}
      };
      var urlString = "http://lista-k8s.eastus.cloudapp.azure.com:5000/api/server/create";
        axios({ method: "GET", "url": urlString }, config).then(result => {
        console.log(result.data);
        objcardCentral.typeSnack = "success";
        objcardCentral.snackText = "Server created " + result.data;
        objcardCentral.snackbar = true;
      }, error => {
        objcardCentral.typeSnack = "error";
        objcardCentral.snackText = error;
        objcardCentral.snackbar = true;
          console.error(error);
      });
      return;
    }

  }
}

</script>

